
import { mapState } from "vuex";
import dayjs from "dayjs";
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Previewer from 'vux/src/components/previewer/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'

import Review from "@/views/TaskInfo/Comments/Comments";
import { backMixin } from "@/common/js/picBack";
import DelData from "@/components/del";
export default {
mixins: [backMixin],
data() {
return {
options: {
saveImg(el) {}
},
types: [
{
key: 0,
value: "电话联系"
},
{
key: 1,
value: "上门拜访"
},
{
key: 2,
value: "微信联系"
}
],
dataObj: {
ImgAttach: []
},
imgFile: [],
recordFile: {},
player: null,
menus: [{ label: "删除", key: 1 }, { label: "修改", key: 2 }],
show: false,
isShow: false,
user: "",
addComParam: {
ID: 0,
CommentType: 2,
DataID: this.$route.params.id,
Content: "",
ReplyID: 0
},
isMsg: false,
isDelData: false,
reviewMsg: {
EvName: "crm-follow",
ImgPath: `${down}/mdtcrm/CRMFollow.jpg`,
Title: "跟进记录",
Content: "您有一条跟进记录的评论",
Params: [this.$route.params.id],
SendUsers: []
}
};
},
mounted() {
if (this.$route.query.backIndex && this.$route.query.backIndex == 1) {
document.addEventListener("plusready", () => {
this.getData(this.$route.params.id);
this.recorder = plus.audio.getRecorder();
this.player = null;
this.$bus.once("onSave", () => {
this.getData(this.$route.params.id);
this.$bus.emit("refrech");
});
});
} else {
this.getData(this.$route.params.id);
this.recorder = plus.audio.getRecorder();
this.player = null;
this.$bus.once("onSave", () => {
this.getData(this.$route.params.id);
this.$bus.emit("refrech");
});
}
},
beforeDestroy() {
this.$bus.off("onSave");
},
beforeRouteEnter(to, from, next) {
let isMsg = false;
if (from.path == "/") {
isMsg = true;
if (window.plus) {
window.setTimeout(() => {
plus.key.removeEventListener("backbutton", () => {});
plus.key.addEventListener("backbutton", () => {
var ws = plus.webview.currentWebview();
plus.webview.close(ws);
});
}, 0);
} else {
document.addEventListener("plusready", () => {
plus.key.removeEventListener("backbutton", () => {});
plus.key.addEventListener("backbutton", () => {
var ws = plus.webview.currentWebview();
plus.webview.close(ws);
});
});
}
}
next(el => {
el.isMsg = isMsg;
});
},






methods: {
clickRight() {
this.isShow = true;
},
back() {
if (this.$route.query.backIndex && this.$route.query.backIndex == 1) {

var ws = plus.webview.currentWebview();
plus.webview.close(ws);
} else {
this.$router.back();
}
},



saveImg(val) {},
goTo() {
let id = this.$route.params.id,
path = this.$route.query.isClient
? "/client/clientDetails/addFollow"
: "/followup/addFollow";
this.$router.push({ path, query: { id } });
},
delData() {
let id = this.$route.params.id;
let _this = this;
this.$vux.confirm.show({
content: "确定删除该条数据？",
onConfirm() {
_this
.postAxios(`/Customer/DeleteFollowUp/${id}`)
.then(res => {
_this.$vux.toast.show({
type: "success",
text: "删除成功"
});
_this.$bus.emit("refrech");
_this.$router.back();
})
.catch(() => {
_this.$vux.toast.show({
type: "cancel",
text: "删除失败"
});
});
}
});
},
getData(id) {
this.getAxios(`/Customer/GetCustomerFollowupByID/${id}`)
.then(res => {
this.dataObj = res.Data;
this.dataObj.Senders.forEach(item => {
this.user += `${item.Name}  `;
});
this.dataObj.ContactType = this.formatType(this.dataObj.ContactType);
this.dataObj.ImgAttach = this.dataObj.ImgAttach || [];
this.dataObj.FollowupDate = dayjs(this.dataObj.FollowupDate).format(
"YYYY-MM-DD HH:mm"
);
this.dataObj.ImgAttach.forEach(item => {
item.src = `${api50}/api/v1/File/DownLoadPic?filePath=${
item.FilePath
}`;
});
this.reviewMsg.SendUsers = [this.dataObj.CreateUserID];
})
.catch(res => {
if (res.ErrorMessage) {




this.isDelData = true;
}
});
},
handleRecord(path, filePath) {
if (filePath) {
this.playRecord(filePath);
} else {
this.download(path, "_doc/audio/").then(filename =>
this.playRecord(filename)
);
}
},

playRecord(path) {
if (this.player) {
this.player.stop();
this.player = null;
return false;
}
this.player = plus.audio.createPlayer(path);
this.player.play(
() => {
this.player.stop();
this.player = null;

},
e => {
this.player.stop();
this.player = null;
}
);
},

download(path, filename, src) {
return new Promise((resolve, reject) => {
let url = src || `${api50}/api/v1/File/DownLoadFile?filePath=${path}`;
const downloader = plus.downloader.createDownload(
url,
{
filename
},
(download, status) => {
if (status == 200) {
resolve(download.filename);
} else {
reject();

}

}
);
downloader.start();
});
},
formatType(key) {
let obj = {};
try {
obj = this.types.find(item => item.key == key);
} catch (err) {
this.types.map(item => {
if (item.key == key) {
obj = item;
}
});
}
return obj.value;
},
isDel(val) {
return (
this.isAdmin ||
(val
? new Date().getTime() - new Date(val.replace(/-/g, "/")).getTime() <=
86400000
: "")
);
}
},
filters: {

picUrl(path, w = 0, h = 0) {
return `${api50}/api/v1/File/DownLoadPic?filePath=${path}&w=${w}&h=${h}`;
},
dateFormat(date) {
return dayjs(date).format("YYYY-MM-DD");
}
},
computed: {
...mapState(["isMyClient", "isAdmin"])
},
components: {
XHeader,
Group,
XInput,
XTextarea,
Previewer,
Actionsheet,
Review,
DelData
}
};



