
import Confirm from 'vux/src/components/confirm/index.vue'

import dayuan from "../../../common/js/dayuan.js";
import Del from "../../../assets/delsy.png";
import Mess from "../../../assets/messy.png";
import { mapState } from "vuex";
export default {
data() {
return {
list: [],
param: {},
contentPlace: "发表新评论",
delcomShow: false,
delID: null,
callStr: "",
dels: Del,
mess: Mess,
timer: null,
comID: 0
};
},
props: {
addParams: {
type: Object
},
msg: {
type: Object
},
workhfBool: {
type: Boolean,
default: false
}
},
components: {
Confirm
},
computed: {
...mapState(["CurrentUserID"])
},
filters: {
imgPath(id) {
return `${apiPic}/dXNlci9sb2dv_${id}_30x30.png`;
}
},
methods: {
addComments() {
if (!this.param.Content) {
this.$vux.toast.show({
text: this.callStr ? "请输入回复内容" : "请输入评论内容！",
type: "warn"
});
return false;
}
if (this.timer) {
clearTimeout(this.timer);
}
this.timer = setTimeout(() => {
this.param.Content = `${this.callStr} ${this.param.Content}`;
this.Axios.post(`/Comment/SaveCrmComment`, this.param).then(res => {
this.param.Content = "";
if (this.callStr) {
this.clearStatus();
this.$vux.toast.show({
text: "回复成功"
});
let copyMsg = { ...this.msg };
copyMsg["SendUsers"] = [this.comID];
if (this.workhfBool) {
copyMsg["Title"] = `您有一条新的任务评论回复消息`;
} else {
copyMsg["Content"] = `您有一条新的跟进评论回复消息`;
}
this.send(copyMsg);
} else {
this.$vux.toast.show({
text: "发布成功"
});
this.send(this.msg);
}
this.getComments();
});
}, 1000);
},
send(m) {
this.Axios.post(`${api50}/api/v1/Common/SendSingleEventMsg`, m)
.then(res => {
this.comID = 0;
})
.catch(err => {
console.log(err, 90);
});
},
clearStatus() {
this.callStr = "";
this.contentPlace = "发表新评论";
this.param.ReplyID = 0;
},
getComments() {
let params = {
dataId: this.param.DataID,
commentType: this.param.CommentType
};
this.Axios.get(`/Comment/GetCrmCommentList`, { params }).then(res => {
this.list = res.data.Data;
});
},
callCom(item) {
this.param.ReplyID = item.ID;
this.contentPlace = `回复 ${item.CreateUserName} 于 ${
item.CreateDate
}的评论`;
this.callStr = `回复 @${item.CreateUserName}:`;
this.comID = item.CreateUserID;
},
emitScroll() {
this.$emit("scrolltoEle");
},
delDia(item) {
this.delcomShow = true;
this.delID = item.ID;
},
delComments() {
this.Axios.post(`/Comment/DeleteCrmCommentInfo/${this.delID}`).then(
res => {
this.getComments();
this.$vux.toast.show({
text: "删除成功"
});
this.clearStatus();
}
);
},
init() {
this.param = Object.assign({}, this.addParams);
this.getComments();
}
},
mounted() {
this.init();
}
};



